/**
 * tab item
 */
interface IProps {
    tabName: string | undefined;
    className: string | undefined;
}

const Tab = (props: IProps): JSX.Element => {
    const { tabName, className } = props;
    return (
        <li className={className} key={tabName}>
            {tabName}
        </li>
    );
};

export default Tab;
