/**
 * article item
 */
import { FIX_AUTHOR } from '../../enmus/local';

interface IProps {
    title: string;
    tags: string[];
    author: string;
    createtime: string;
    body: string;
}

const SectionItem = (props: IProps): JSX.Element => {
    const { title, tags, author, createtime, body } = props;
    return (
        <div className="section_box">
            <h2 className="article_title">{title}</h2>
            <ul className="article_tags_and_author">
                <li className="article_author">{`${FIX_AUTHOR}${author}`}</li>
                <li className="article_created">{createtime}</li>
                <li className="article_tags">
                    {tags.map((el) => (
                        <span key={el}>{el}</span>
                    ))}
                </li>
            </ul>
            <p className="article_body">{body}</p>
        </div>
    );
};

export default SectionItem;
